
import { Item, OrderChoice } from "@/interface";
import { defineComponent, PropType, ref } from "vue";
import ItemComponent from "../components/ItemComponent.vue";

export default defineComponent({
  props: {
    items: {
      type: Object as PropType<Item[]>,
      required: true,
    },
  },
  emits: ["showItemDetail"],
  computed: {
    filteredItems(): Item[] {
      return this.items.filter((item: Item) => item.orderList.length > 0);
    },
  },
  components: {
    ItemComponent,
  },
  methods: {
    showItemDetail(code: string) {
      this.$emit("showItemDetail", code);
    },
    updateOrderChoiceQty(choice: OrderChoice, qty: number) {
      choice.qty += qty;
    },
  },
});
